import React, { Suspense, useEffect, useRef, lazy } from 'react'
import { themeImpressionTracking } from '../../../../utils'
import { isTagShop } from "../../../../actions/api";
import AuthorDetails from './AuthorDetails';
import Media from './Media';
import CTAButton from '../../Elements/CTA/CtaButton';
import SocialAction from '../../Elements/SocialAction/SocialAction';

const ShareOnHover = lazy(() => import('../../Elements/ShareOnHover/ShareOnHover'));
const Card = ({ adjustWidth, clickToShowPopUp, index, onClickToCTA, onClickPopUpSlider, ThemeStyle, CardStyle, Post, ThemeInfo }) => {
  const postTrack = useRef(null);
  const cardStyle = { width: `${adjustWidth}%`, padding: CardStyle.style.padding / 2 };
  const mediaType = Post.type !== 1 ? true : false; 

  useEffect(() => {
    if (isTagShop) themeImpressionTracking(postTrack);
  }, []);


  return <div id={`tb_cp_id-${Post.id}`} className={`tb_cp_post_wrapper`} style={cardStyle} tb-network={Post.networkId} tb-product={ Post.isShopIcon ? 1 : Post.isHotspot ? 2 : 0} ref={postTrack} post-id={Post.referenceId ? Post.referenceId : Post.id} feed-id={Post.feedId} tabIndex={0} area-label={Post.content.text} role="article">
    <div className="tb_cp_post_in" onClick={clickToShowPopUp(index, Post)} role="button" aria-label="Popup" tabIndex={0}>
      <div className="tb_cp_post_media_wrapper">
        {CardStyle.shareStatus ? 
          <Suspense fallback={<></>}> 
            <ShareOnHover Post={Post} ThemeInfo={ThemeInfo} shareClass={'tb_cp_share_container'} />
          </Suspense> 
        : ''}
        {mediaType ? 
            <Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} index={index} />
        : ''}
        { Post.ctaStatus ? <div className="tb_cp_post_cta">
            <CTAButton ctaClass={'tb_cp_post_cta_btn'} Post={Post} onClickToCTA={onClickToCTA} />
        </div> : ''}
      </div>
      <div className="tb_cp_contant_wrapper">
          <AuthorDetails Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} />
      </div>
      {CardStyle.actionStatus ? 
          <SocialAction Post={Post} CardStyle={CardStyle} actionClass={`tb_cp_social_action__`} />
      : ''}
    </div>
  </div>
}
export default Card