import React from 'react'
import { isInstaUser } from '../../../../utils';
import Author from '../../Elements/Author/Author';
import Time from '../../Elements/Time/Time';
import Network from '../../Elements/Network/Network';

  const AuthorDetails = ({ Post, CardStyle }) => {
    const { author, networkId } = Post
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const authorStatus = (CardStyle.author.status && !isInstaUser(author.name)) ? true : false;
  const sepratorStatus = (authorStatus && CardStyle.timeStatus) ? true : false;
  return <div className="tb_cp_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    <div className="tb_cp_author">
      {authorStatus ? 
          <Author author={author} networkId={networkId} authorClass={'tb_cp_author_profile'} />
      : ''}

      <div className="tb_cp_author_info">
        {authorStatus ? <div className="tb_cp_authorname">{author.name}</div> : ''}
        <div className="tb_cp_post_info">
          {authorStatus ? <div className="tb_cp_username">{username}</div> : ''}
          {sepratorStatus ? <div className="tb_cp_seprator"> </div> : ''}
          {CardStyle.timeStatus ? 
              <Time createdAt={Post.createdAt} timeClass={`tb_cp_time tb-cTBfont-${CardStyle.author.variant}`} />
          : ''}
        </div>
      </div>
    </div>
    <div className="tb_cp_social_">
      <Network networkClass={'tb_cp_social_ico'} networkId={networkId} iconStyle={CardStyle.icon} />
    </div>
  </div>
}
export default AuthorDetails