import React from 'react'
import { isTagShop } from "../../../../actions/api";
import Video from '../../Elements/Media/Video';
import Image from '../../Elements/Media/Image';
import MediaIcon from '../../Elements/MediaIcon/MediaIcon';

const Media = ({ Post, ThemeStyle, CardStyle, index }) => {
  const autoPlay = ([3,5].includes(Post.type) && ThemeStyle.autoPlay === 1) || false
  const size = CardStyle.style.ratio ? CardStyle.style.ratio : false
  const mediaIcon = isTagShop && Post.hotspot ? false : true

  return <div className="tb_cp_media_wrap">
     <MediaIcon Post={Post} IconClass="tb_cp_media_icon" show={mediaIcon}hideVideo={autoPlay}/>
    {autoPlay ?
      <Video VideoClass="tb_cp_video" Post={Post} size={size} isCover={true} controls={false} autoPlay={true} muted={true} observer={true} index={index}/>
      :
      <Image ImageClass="tb_cp_image" Post={Post} size={size} hotspot={isTagShop} blurBG={isTagShop} observer={true} index={index}/>
    }
  </div>
}
export default Media