import React, { Component, memo } from "react";
import ReactPlayer from "react-player";
import { isSocialWall, isTagShop } from "../../../../actions/api";
import ReactHtmlParser from "react-html-parser";
import { GET_IMAGE_SIZE } from "../../../../utils/Common";
class Video extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loadError: false,
            videoLoaded: false,
            isVisible: !this.props.observer,
            calcHeight: 0,
            calcWidth: 0,
        };
        this.mediaRef = React.createRef();
        this.observer = null;
        this._isMounted = false; // Track component mount status
        this.abortController = new AbortController(); // For canceling fetch requests
    }

    componentDidMount() {
        this._isMounted = true;
        this.mediaSizeCalc();

        // Intersection Observer for lazy loading
        try {
            this.observer = new IntersectionObserver(
                ([entry]) => {
                    if (this._isMounted) {
                        this.setState({ isVisible: true });
                    }
                },
                { threshold: 0.1 }
            );

            if (this.mediaRef.current) this.observer.observe(this.mediaRef.current);
        } catch (error) {
            if (this._isMounted) {
                this.setState({ isVisible: true });
            }
        }
    }

    componentWillUnmount() {
        this._isMounted = false;

        if (this.observer) {
            this.observer.disconnect();
        }
        // Clean up Intersection Observer
        if (this.observer && this.mediaRef.current) {
            this.observer.unobserve(this.mediaRef.current);
        }

        // Abort any ongoing fetch requests
        this.abortController.abort();
    }

    mediaSizeCalc = async () => {
        const { media } = this.props.Post;

        if (!media?.height && !media?.width) {
            try {
                const { width, height } = await GET_IMAGE_SIZE(media?.image?.small, {
                    signal: this.abortController.signal, // Pass AbortController signal
                });

                if (this._isMounted) {
                    this.setState({
                        calcHeight: height,
                        calcWidth: width,
                    });
                }
            } catch (error) {
                if (this._isMounted && error.name !== "AbortError") {
                    this.setState({
                        calcHeight: 300,
                        calcWidth: 300,
                    });
                }
            }
        }
    };

    setVideoLoaded = (event) => {
        if (this._isMounted) {
            this.setState({ videoLoaded: event });
        }
    };

    render() {
        const { VideoClass, Post, controls, autoPlay, handleVideoEnded, muted, intervalTime, size } = this.props;
        const { isVisible, calcHeight, calcWidth } = this.state;
        const { media, id, link, networkId } = Post
        const fullsize = size === 1;
        const height = media?.height ? media?.height : calcHeight;
        const width = media?.width ? media?.width : calcWidth;
        const paddingSize = size ? size : (height * 100) / width;
        const videoStyle = {
            paddingBottom: `${fullsize ? null : paddingSize}%`,
        };

        return (
            <div className={`${VideoClass}_wrap_`} style={videoStyle} ref={this.mediaRef}> 
                <ReactPlayer
                    className={`${VideoClass} tb_media-${id}`}
                    url={isVisible ? media?.video?.full : media?.image?.small}
                    data-height={height}
                    data-width={width}
                    data-type="video"
                    data-network={networkId}
                    data-link={link}
                    data-item-id={id}
                    data-load={0}
                    config={{
                        facebook: {
                            appId: '117029975354072',
                            attributes: {
                                'data-width': width,
                                'data-height': height,
                                poster: media?.image?.small,
                            },
                        },
                        file: {
                            attributes: {
                                preload: 'metadata',
                                poster: media?.image?.small,
                            },
                        },
                    }}
                    loop={isSocialWall && !isTagShop && Post?.loopStop ? false : autoPlay}
                    onError={(e) => {
                        if (fullsize && handleVideoEnded) {
                            setTimeout(() => {
                                if (this._isMounted && handleVideoEnded) handleVideoEnded();
                            }, intervalTime * 1000);
                        }
                        this.setVideoLoaded(false);
                    }}
                    autoPlay={isVisible && autoPlay}
                    muted={muted}
                    volume={autoPlay ? 1 : 0}
                    playsinline={true}
                    onReady={() => this.setVideoLoaded(!isTagShop)}
                    playing={isVisible && autoPlay}
                    controls={controls || false}
                    onEnded={handleVideoEnded}
                    title={ReactHtmlParser(media?.title)}
                />  
            </div>
        );
    }
}

export default memo(Video);